<template lang="pug">
    .departments__content
        .departments__title Департаменты
        ul.departments__list
            li.departments__item(v-for="department in departments" :key="department.id")
                department-item(
                    :department='department'
                )
        button(@click.prevent='addDepartment' id='addDepartment').add__department Добавить департамент
        add-department-form(
                v-if='addDepartmentFormVisible'
                v-on:eventNewDepartment='eventNewDepartment'
            )

</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
    components: {
        departmentItem: () => import("../../admin/departments/department-item"),
        addDepartmentForm: () => import("../../admin/departments/add-department-form")
    },
    created() {
        this.fetchDepartments()
    },
    computed: {
        ...mapState ("departments", {
            departments: state => state.departments
        }
        )
    },
    data () {
        return {
            addDepartmentFormVisible: false
        }
    },
    methods: {
        ...mapActions("departments", ["fetchDepartments"]),
        addDepartment() {
            this.addDepartmentFormVisible = !this.addDepartmentFormVisible 
            document.getElementById('addDepartment').setAttribute("disabled", "true")
        },
        eventNewDepartment() {
            this.addDepartmentFormVisible=!this.addDepartmentFormVisible
            document.getElementById('addDepartment').removeAttribute("disabled")
      }
    }
}
</script>
<style lang="scss">
.departments__content{
    width: 100%;
}
