    display: flex;
    flex-direction: column;
    justify-content: center;
}
.departments__item {
    border-bottom: 1px solid;
    display: flex;
    align-items: center;
    background: #faf4e8;
    border-radius: 6px;
    margin-bottom: 5%;
    padding: 18px;
    margin-bottom: 10px;
    position: relative;
    $animals: 'elk', 'elephant1', 'horse', 'hippopotamus1', 'koala', 'bird1', 'lemur', 'giraffe', 'crocodiles', 'butterfly', 'zebra1';
    @for $i from 1 through length($animals) {
        $item: nth($animals, $i);
        &:nth-child(11n+#{$i}){
            position: relative;
            &:after {
                content: '';
                display: block;
                position: absolute;
                background: url(../../../assets/images/icons/#{$item}.svg) center center no-repeat;
                background-size: contain;
                top: 0;
                left: 0;
                width: 45px;
                height: 45px;
                border-radius: 4px;
            }
        }
    }
    &:nth-child(even) {
        &:after {left: 43px;}}
}
.add__department {
    padding: 1%;
    border-radius: 6px;
    background: #464549;
    color: #faf4e8;
    margin-bottom: 2%;
    &:hover {
        opacity: .8;
        transition: .5s;
        cursor: pointer;
    }
}
</style>